@import url(https://fonts.googleapis.com/css2?family=Heebo&family=Space+Mono:wght@400;700&display=swap);

button {
    position: relative;
    height: 65px;
    width: 80px;
    color: white;
    background-color: #4d4d4d;
    border: none;
    font-size: 20px;
    outline: 1px solid black;

}

button:hover {
    color: black;
    outline: 0.05rem solid gray;
    z-index: 3;
}

#clear,
#zero {
    width: 160px;
}

#equals {
    position: absolute;
    height: 130px;
    /* height: 65px; */
    background-color: #004466;
    margin-top: -65px;
}
#clear {
    background-color: darkred;
}
.formulaScreen {
    min-height: 30px;
    text-align: right;
    padding: 5px;
    /* margin-bottom: 5px; */
    font-size: 1.5rem;
    color: orange;
    word-wrap: break-word;
    /* border: 2px solid yellow; */
}
#display {
    text-align: right;
    font-size: 1.7rem;
    padding: 5px;
    color: white;
}
* {
    padding: 0px;
    margin: 0px;
    /* box-sizing: border-box; */
}

body {
    background-color: #c2c2d6;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: default;
}

#app {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(151, 187, 207);
}

#calculator {
    position: relative;
    width: 320px;
    padding: 2px;
    outline: 2px solid white;
    background-color: rgb(0, 0, 0);
    border-radius: 5px;
    font-family: 'Heebo', sans-serif;
    font-family: 'Space Mono', monospace;
}

#footer {
    text-align: center;
    padding: 20px;
    font-family: 'MV Boli', sans-serif;
}

@media only screen and (max-width: 599px) {

    /*for extra small and small devices such as mobile phones, tablets*/
    #calculator {
        width: 98vw;
    }

    button {
        width: 25%;
    }

    #clear,
    #zero {
        width: 50%;
    }
}
